.img {
	width: 920px;
	height: 305px;
	border-radius: 15px;
	position: relative;
	overflow: hidden;
	background-color: rgb(51, 51, 148);
	display: flex;
	flex-direction: row-reverse;
}

.img__overlay {
	position: absolute;
	top: 0;
	left: 0;
	padding: 20px;
	height: 305px;
	box-sizing: border-box;
	color: white;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
}

.img__title {
	font-size: 40px;
	line-height: 108%;
	margin: 0 0 10px 0;
}

.img__text {
	padding: 0 0 130px 0;
}

.img__image {
	width: 450px;
	height: 305px;
	object-fit: cover;
}
